const swiperMain = new Swiper(".story__swiper", {
  spaceBetween: 30,
  pagination: {
    el: ".story__swiper-pagination",
    clickable: true,
  },
});



const swiperManual = new Swiper('.manual__swiper', {
  freeMode: true,
  loop: true,
  spaceBetween: 30,
  slidesPerView: 3,

  // Navigation arrows
  navigation: {
    nextEl: '.manual__swiper-button-next',
    prevEl: '.manual__swiper-button-prev',
  },

  breakpoints: {
    // when window width is >= 320px
    320: {
      slidesPerView: 1,
      spaceBetween: 10
    },

    565: {
      slidesPerView: 3,
      spaceBetween: 30
    }
  }
});
